.App {
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  background-color: #333;
  background: #333 url('./images/09_540_blur.jpg') center center no-repeat;
  background-size: cover;
}

.loadingContainer {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.charactersContainer {
  display: flex;
  align-items: flex-start;
  min-height: 60vh;
  width: 100%;
  padding-top: 3em;
  box-sizing: border-box;
}

.characters {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 100%;
}

.loadingStatus {
  color: white;
}